.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

h1, h2, h3, h4 {
    color: black;
    margin: auto;
    text-align: center;
    width: 50%;
    margin-top: 10px;
  }

  h1 {
      font-size: 32px;
  }
  
  h2 {
      font-size: 24px;
  }

  h3 {
      font-size: 18.72px;
  }
  h4 {
    font-size: 16px;
    margin-top: 0px;
  }
